<script>
  import { numberWithCommas } from "./../../globals.js";
  import axios from "axios";
  import { onMount, afterUpdate } from "svelte";
  import { params } from "@sveltech/routify";
  import moment from "moment";
  import Chart from "chart.js";
  export let name;

  let requestedData;

  let startDate = moment().subtract(40, "days");
  let endDate = moment().subtract(1, "days");

  let chartData = {};
  let labels = [];

  let cases = [];
  let deaths = [];
  let recovered = [];

  let ctx;

  let nameOfCountry = "";

  onMount(() => {
    axios.get("https://restcountries.eu/rest/v2/alpha/" + name).then(res => {
      nameOfCountry = res.data.name;
    });
    axios
      .get(`https://corona.lmao.ninja/v2/historical/${name}?lastdays=40`)
      .then(res => {
        requestedData = res.data.timeline;
        Object.keys(requestedData.cases).forEach(key => {
          labels = [...labels, moment(key, "M/DD/YY").format("DD/MM/YY")];
          cases = [...cases, requestedData.cases[key]];
        });

        Object.keys(requestedData.deaths).forEach(key => {
          deaths = [...deaths, requestedData.deaths[key]];
        });

        Object.keys(requestedData.recovered).forEach(key => {
          recovered = [...recovered, requestedData.recovered[key]];
        });
        let myChart = new Chart(ctx.getContext("2d"), {
          type: "line",
          data: {
            labels: labels,
            datasets: [
              {
                label: "total cases",
                data: cases,
                pointRadius: 5,
                pointHoverRadius: 5,
                pointBackgroundColor: "gray",
                backgroundColor: ["transparent"],
                borderColor: ["rgb(179, 179, 0)"],
                borderWidth: 3
              },
              {
                label: "deaths",
                data: deaths,
                pointRadius: 5,
                pointHoverRadius: 5,
                pointBackgroundColor: "gray",
                backgroundColor: ["transparent"],
                borderColor: ["red"],
                borderWidth: 3
              },
              {
                label: "recovered",
                data: recovered,
                pointRadius: 5,
                pointHoverRadius: 5,
                pointBackgroundColor: "gray",
                backgroundColor: ["transparent"],
                borderColor: ["green"],
                borderWidth: 3
              }
            ]
          },
          options: {
            tooltips: {
              yPadding: 10,
              bodySpacing: 10,
              titleSpacing: 5,
              titleAlign: "center",
              bodyFontSize: 13,
              titleFontSize: 15,
              caretSize: 1,
              displayColors: false,
              callbacks: {
                label: function(tooltipItem, data) {
                  if (tooltipItem.datasetIndex == 0) {
                    return numberWithCommas(tooltipItem.value) + " cases";
                  }
                  if (tooltipItem.datasetIndex == 1) {
                    return numberWithCommas(tooltipItem.value) + " deaths";
                  }
                  if (tooltipItem.datasetIndex == 2) {
                    return numberWithCommas(tooltipItem.value) + " recovered";
                  }
                },
                afterLabel: function(tooltipItem, data) {
                  if (tooltipItem.datasetIndex == 0) {
                    let firstValue =
                      data["datasets"][0]["data"][tooltipItem["index"] - 1];
                    let value =
                      data["datasets"][0]["data"][tooltipItem["index"]];

                    if (firstValue) {
                      return (
                        "increase of " +
                        (((value - firstValue) / value) * 100).toFixed(2) +
                        "% \n" +
                        numberWithCommas(value - firstValue) +
                        " new cases"
                      );
                    }
                  } else if (tooltipItem.datasetIndex == 1) {
                    let firstValue =
                      data["datasets"][1]["data"][tooltipItem["index"] - 1];
                    let value =
                      data["datasets"][1]["data"][tooltipItem["index"]];

                    if (firstValue) {
                      return (
                        "increase of " +
                        (((value - firstValue) / value) * 100).toFixed(2) +
                        "% \n" +
                        numberWithCommas(value - firstValue) +
                        " new deaths"
                      );
                    }
                  } else {
                    let firstValue =
                      data["datasets"][2]["data"][tooltipItem["index"] - 1];
                    let value =
                      data["datasets"][2]["data"][tooltipItem["index"]];

                    if (firstValue) {
                      return (
                        "increase of " +
                        (((value - firstValue) / value) * 100).toFixed(2) +
                        "% \n" +
                        numberWithCommas(value - firstValue) +
                        " new recovered"
                      );
                    }
                  }
                }
              }
            }
          }
        });
      });
  });
</script>

<style lang="scss">.flag {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 3rem; }
  .flag .name {
    font-weight: bold;
    font-size: 2rem;
    letter-spacing: 0.5mm;
    margin-bottom: 1rem; }
  .flag img {
    height: 6rem;
    width: 8rem;
    border: 1px solid gray; }

.chart-container {
  display: flex;
  width: 80%;
  margin: 0 auto;
  padding: 3rem;
  border: 1px solid lightgrey; }
/*# sourceMappingURL=src/pages/country/[name].svelte.map */</style>

<div class="flag">
  <div class="name">{nameOfCountry}</div>
  <img
    alt="flag"
    src={`https://flagpedia.net/data/flags/h160/${name.toLowerCase()}.webp`} />
</div>
<div class="chart-container">
  <canvas bind:this={ctx} width="1000" height="500" />
</div>
